<template>
  <div class="main-ctn">
    <el-row>
      <el-col :span="24">
        <div class="grid-content bg-purple">
          <div class="main-content clearfix">
            <el-form :label-position="labelPosition" :inline="true" label-width="250px" :rules="rules" :model="addDetail" ref="formName">
              <el-form-item label=""  prop="staffname">
                <el-input v-model="addDetail.staffname" placeholder="" name="createStaffId" :disabled="true"></el-input>
              </el-form-item>
              <el-form-item label=""  prop="grade">
                <el-input v-model.number="addDetail.grade" placeholder="" name="grade"></el-input>
              </el-form-item>
              <el-form-item label="" prop="staffGroupType">
              <template>
                <el-select v-model="addDetail.staffGroupType" placeholder=""  >
                   <el-option v-for="item in staffGroupType" :key="item.typeId" :label="item.typeValue"  :value="item.typeId"></el-option>
                </el-select>
              </template>
              </el-form-item>
              <el-form-item label="" prop="visitCreateDate" >
               <template>
                 <el-date-picker  v-model="addDetail.visitCreateDate" type="date"  placeholder="" value-format="yyyy-MM-dd">
                 </el-date-picker>
               </template>
              </el-form-item>               
              <el-form-item label="" prop="collectRemark" class="collect">
                <el-input type="textarea" v-model="addDetail.collectRemark"></el-input>
              </el-form-item>
            </el-form>
            <span slot="footer" class="weekly-footer">
              <el-button @click="adddetail(addDetail,'formName')" ></el-button>
              <el-button type="primary" @click="cancel()"></el-button>
            </span>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import service from "../scripts/service.js";
require("kindeditor");
require("kindeditor/themes/default/default.css");
export default {
  data: function() {
    return {
      rules: {
        grade: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        staffGroupType: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        visitCreateDate: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        collectRemark: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ]
      },
      labelPosition: "right",
      addDetail: {
        staffGroupType: ""
      },
      Contents: "",
      Contentss: "",
      Contentsss: "",
      subjectName: "",
      staffname: "",
      staffID: "",
      editDetail: {},
      weeklyType: [],
      staffGroupType: [],
      subjectClass: [],
      staffType: [],
      weeklyDetail: {}
    };
  },
  mounted: function() {
    this.getAauthor();
    this.getselect();
  },
  methods: {
    getselect() {
      service
        .getListGroupType()
        .then(res => {
          this.staffGroupType = res.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    getAauthor() {
      service
        .getListSxByGType()
        .then(res => {
          console.log(res);
          this.Aauthor = res.data[4];
          console.log(this.Aauthor);
          this.staffname = this.Aauthor.staffname;
          this.staffID = this.Aauthor.staffId;
          console.log(this.staffname);
          this.addDetail = {
            subjectName: this.subjectName,
            staffname: this.staffname,
            staffGroupType: "",
            visitCreateDate: "",
            collectRemark: ""
          };
        })
        .catch(err => {
          console.log(err);
        });
    },
    //
    cancel() {
      window.close();
    },
    //
    adddetail(addDetail, formName) {
      this.$refs["formName"].validate(valid => {
        if (valid) {
          this.addDetail = {
            checkStaffId: this.$route.query.checkStaffId,
            staffId: this.staffID,
            visitCreateDate: this.addDetail.visitCreateDate,
            grade: this.addDetail.grade,
            staffGroupType: this.addDetail.staffGroupType,
            collectRemark: this.addDetail.collectRemark
          };
          console.log(this.addDetail);
          let params = {
            csc: this.addDetail
          };
          console.log(params);
          service
            .addCheckStaffComment(params)
            .then(response => {
              alert("");
              window.close();
            })
            .catch(error => {
              console.log(error);
            });
        } else {
          return false;
        }
      });
    }
  }
};
</script>

<style scoped>
.main-ctn {
  background: #f8f8f8;
}
.bg-purple {
  background: #ffffff;
  border-radius: 8px;
}
.main-content {
  padding-top: 30px;
}
.el-form-item {
  margin-top: 5px;
}
.main-content .dialog-footer {
  margin: 0 auto;
}
.weekly-footer {
  float: left;
  margin-left: 40% !important;
  margin-bottom: 20px !important;
}
.knowledge-content-editor {
  width: 110%;
}
.updateweekly {
  margin-top: 5px;
  float: left;
  font-weight: 400;
  color: #66666f;
}
.selfile {
  margin-left: 10px !important;
}
.upload-demo {
  margin-left: 58px;
  width: 80%;
  padding-bottom: 20px;
}
</style>


